<template>
  <div>
    <p class="error" v-if="error">{{ error }}</p> 
    <b-container>
      <b-row>
        <b-col>
          <h1>Number of uses: {{ topStats[0].recordedcount }}</h1>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import StatsService from '../StatsService'

export default {
  name: 'TopStats',
  data () {
    return {
      topStats: [],
      error: ''
    }
  },
  async created() {
    try {
      this.topStats = await StatsService.getTotalTriggers()
    } catch(err) {
      this.error = err.message
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

p.error {
  border: 1px solid #ff5b5f; background-color: #ffc5c1; padding: 10px; margin-bottom: 15px;
}

</style>